import axois from '@/components/axios'

const GET_APP_LIST = 'GET_APP_LIST'
const GET_EVENT_DETAIL = 'GET_EVENT_DETAIL'

const state = {
  app: {
    list: [],
    total_count: 0
  },
  detail: {
    timeLine: []
  }
}

const mutations = {
  [GET_APP_LIST] (state, payload) {
    state.app.list = payload.list
    state.app.total_count = payload.total_count
  },
  [GET_EVENT_DETAIL] (state, payload) {
    state.detail.timeLine = payload.timeline
  }
}

const getters = {
  app: state => state.app,
  detail: state => state.detail
}

const actions = {
  getAppList: ({ commit, state }, params) => {
    return new Promise((resolve, reject) => {
      axois({
        method: 'post',
        url: 'api/app/list',
        data: {
          ...params
        }
      }).then(res => {
        let {datas: {list, total}} = res.data
        let rs = list.map(ele => {
          return {
            id: ele.ID,
            appeui: ele.APPEUI,
            name: ele.NAME,
            longitude: ele.CENTER_LNG,
            latitude: ele.CENTER_LAT,
            zoom: ele.ZOOM,
            status: ele.STATUS
          }
        })
        commit('GET_APP_LIST', {
          list: rs,
          total_count: total
        })
        resolve(res.data)
      })
    })
  },
  addApp: ({ commit, state }, params) => {
    return new Promise((resolve, reject) => {
      axois({
        method: 'post',
        url: 'api/app/add',
        data: {
          ...params
        }
      }).then(res => {
        if (res.data && res.data.result === 0) {
          resolve(res.data)
        }
      })
    })
  },
  deleteApp: ({ commit, state }, params) => {
    return new Promise((resolve, reject) => {
      axois({
        method: 'delete',
        url: 'api/app/delByAppId',
        params: {
          ...params
        }
      }).then(res => {
        if (res.data && res.data.result === 0) {
          resolve(res.data)
        }
      })
    })
  },
  updateApp: ({ commit, state }, params) => {
    return new Promise((resolve, reject) => {
      axois({
        method: 'PATCH',
        url: 'api/app/update',
        data: {
          ...params
        }
      }).then(res => {
        if (res.data && res.data.result === 0) {
          resolve(res.data)
        }
      })
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}



