import {Message} from 'element-ui'
import axios from 'axios'
import store from '@/store'

axios.defaults.timeout = 60000
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8'
axios.defaults.headers.patch['Content-Type'] = 'application/json;charset=UTF-8'
axios.defaults.headers.get['Content-Type'] = 'application/json;charse=UTF-8'
axios.defaults.headers.delete['Content-Type'] = 'application/json;charse=UTF-8'
// http://192.168.1.117:8081 http://zhutest.qmant.com/
// axios.defaults.baseURL = (process.env.NODE_ENV === 'development' ? '' : '')
// axios.defaults.baseURL = (process.env.NODE_ENV === 'development' ? 'http://106.14.193.181:80/monitor/' : 'http://106.15.229.52:8080/')
axios.defaults.withCredentials = false

// POST传参序列化
axios.interceptors.request.use((config) => {
  if (store.state && store.state.common && store.state.common.userInfo && store.state.common.userInfo.token) {
    if (config.url.indexOf('https://restapi.amap.com/v3/geocode/geo') !== 0) {
      config.headers.common = Object.assign({}, config.headers.common, {'Authorization': `Bearer ${store.state.common.userInfo.token}`})
    }
  }
  if (config.method === 'post' || config.method === 'patch') {
    config.data = JSON.stringify(config.data)
  }
  // 在发送请求之前做些什么
  // 随便写个值 绕过if判段
  if (config.method === 'get' || config.method === 'delete') {
    config.data = true
  }
  return config
})

axios.interceptors.response.use((res) => {
  // token失效得判断
  if (res.status === 401) {
    Message({
      message: '登陆过期,请重新登陆',
      type: 'error',
      onClose () {
        window.location.href = '/login'
      }
    })
    return Promise.reject(res)
  } else if (res.data.result !== 0) {
    Message({
      message: res.data.message,
      type: 'error'
    })
    // return Promise.reject(res)
  }
  return res
}, (error) => {
  if (error.response.status === 403) {
    Message({
      message: '登陆过期,请重新登陆',
      type: 'error',
      onClose () {
        let loginUrl = window.location.origin + '/#/login'
        window.location.href = loginUrl
      }
    })
    return Promise.reject(error)
  }
  Message({
    message: '网络异常',
    type: 'error'
  })
  return Promise.reject(error)
})

export default axios



