<template>
  <div class="echarts">
    <IEcharts
      :option="bar"
      :loading="loading"
      @ready="onReady"
      @click="onClick"
    />
  </div>
</template>

<script type="text/babel">
import IEcharts from 'vue-echarts-v3/src/full.js'
export default {
  name: 'bar',
  components: {
    IEcharts
  },
  props: {
    name: {
      type: String,
      required: true,
      default: 'bar'
    },
    xValue: {
      type: Array,
      required: true,
      default: () => []
    },
    series: {
      type: Array,
      required: true,
      default: () => {
        return [
          {
            name: 'In',
            type: 'bar',
            data: [2, 5, 7, 9]
          }, {
            name: 'Out',
            type: 'bar',
            data: [1, 10, 6, 12],
            itemStyle: {
              normal: {
                color: '#F1B31C'
              }
            }
          }
        ]
      }
    },
    direction: {
      type: String,
      default: 'v'
    }
  },
  computed: {
    bar: function () {
      return {
        title: {
          text: this.name,
          subtext: '',
          textStyle: {
            color: '#fff'
          }
        },
        tooltip: {
          trigger: 'axis'
        },
        /* legend: {
            data: ['In', 'Out']
          }, */
        toolbox: {
          show: true,
          feature: {
            // dataView: {show: true, readOnly: false},
            // magicType: {show: true, type: ['line', 'bar']},
            // restore: {show: true},
            // saveAsImage: {show: true}
          }
        },
        calculable: true,
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: this.config.x.type,
            data: this.config.x.data,
            axisLabel: {
              textStyle: {
                color: '#fff'
              }
            }
          }
        ],
        yAxis: [
          {
            type: this.config.y.type,
            data: this.config.y.data,
            axisLabel: {
              textStyle: {
                color: '#fff'
              }
            }
          }
        ],
        series: this.series
      }
    },
    config: function () {
      const conf = {
        v: {
          x: {
            type: 'category',
            data: this.xValue
          },
          y: {
            type: 'value',
            data: null
          }
        },
        h: {
          x: {
            type: 'value',
            data: null
          },
          y: {
            type: 'category',
            data: this.xValue
          }
        }
      }
      return conf[this.direction]
    }
  },
  data: () => ({
    loading: false
  }),
  methods: {
    doRandom () {
      const that = this
      let data = []
      for (let i = 0, min = 5, max = 99; i < 4; i++) {
        data.push(Math.floor(Math.random() * (max + 1 - min) + min))
      }
      that.loading = !that.loading
      that.bar.series[0].data = data
    },
    onReady (instance, ECharts) {
      console.log(instance, ECharts)
    },
    onClick (event, instance, ECharts) {
      console.log(arguments)
    }
  },
  created () {
  }
}
</script>

<style scoped>
  .echarts {
    height: 400px;
  }
</style>



