export default {
  path: '/network',
  name: 'network',
  title: '网络管理',
  icon: 'iconfont icon-home',
  redirect: '/network/config',
  component: r => require.ensure([], () => r(require('@/pages/network/index'))),
  children: [{
    path: 'config',
    name: 'config',
    component: r => require.ensure([], () => r(require('@/pages/network/config')))
  }, {
    path: 'monitor',
    name: 'monitor',
    component: r => require.ensure([], () => r(require('@/pages/network/monitor')))
  }]
}



