export default {
  path: '/app',
  name: 'app',
  title: '应用管理',
  icon: 'iconfont icon-home',
  redirect: '/app/list',
  component: r => require.ensure([], () => r(require('@/pages/app/index'))),
  children: [{
    path: 'list',
    name: 'appList',
    component: r => require.ensure([], () => r(require('@/pages/app/list')))
  }, {
    path: 'userAdmin',
    name: 'userAdmin',
    component: r => require.ensure([], () => r(require('@/pages/app/userAdmin')))
  }]
}



