<template>
  <div class='echarts'>
    <IEcharts :option='pie' :loading='loading' @ready='onReady' @click='onClick' />
  </div>
</template>

<script type='text/babel'>
import IEcharts from 'vue-echarts-v3/src/full.js'
export default {
  name: 'pie',
  components: {
    IEcharts
  },
  props: {
    name: {
      type: String,
      required: false,
      default: 'pie'
    },
    series: {
      type: Array,
      required: true,
      default: () => {
        return [
          {
            name: '访问来源',
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: [
              { value: 335, name: '直接访问' },
              { value: 310, name: '邮件营销' },
              { value: 234, name: '联盟广告' },
              { value: 135, name: '视频广告' },
              { value: 1548, name: '搜索引擎' }
            ]
          }
        ]
      }
    },
    legend: {
      type: Array,
      required: false,
      default: () => ['直接访问', '邮件营销', '联盟广告', '视频广告', '搜索引擎']
    }
  },
  computed: {
    pie: function () {
      return {
        title: {
          text: this.name,
          // subtext: '纯属虚构',
          x: 'center',
          textStyle: {
            color: '#fff'
          }
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          x: 'left',
          data: this.legend,
          textStyle: {
            color: '#fff'
          }
        },
        toolbox: {
        },
        calculable: true,
        series: this.series
      }
    }
  },
  data: () => ({
    loading: false
  }),
  methods: {
    onReady (instance, ECharts) {
      console.log(instance, ECharts)
    },
    onClick (event, instance, ECharts) {
      console.log(arguments)
    }
  }
}
</script>

<style scoped>
.echarts {
  height: 280px;
}
</style>



