export default {
  path: '/monitor',
  name: 'monitor',
  title: '监控台',
  icon: 'iconfont icon-home',
  redirect: '/monitor/card',
  component: r => require.ensure([], () => r(require('@/pages/monitor/index'))),
  children: [{
    path: 'data',
    name: 'dataMode',
    component: r => require.ensure([], () => r(require('@/pages/monitor/dataMode')))
  }, {
    path: 'map',
    name: 'mapMode',
    component: r => require.ensure([], () => r(require('@/pages/monitor/mapMode')))
  }, {
    path: 'card',
    name: 'cardMode',
    component: r => require.ensure([], () => r(require('@/pages/monitor/cardMode')))
  }]
}



