import axois from '@/components/axios'

const GET_DEVICE_LIST = 'GET_DEVICE_LIST'
const GET_ALARM_REASONS = 'GET_ALARM_REASONS'
const CLRAR_ALL_DATA = 'CLRAR_ALL_DATA'
const GET_SENSOR_TYPES = 'GET_SENSOR_TYPES'
const GET_SENSOR_STATUS = 'GET_SENSOR_STATUS'
/** 设备列表分页配置 */
export const DEVICE_LIST_CONFIG = 'DEVICE_LIST_CONFIG'

const state = {
  features: [],
  device: {
    list: [],
    page_number: 1,
    page_size: 20,
    total_count: 0
  },
  alarmReasons: [],
  sensorTypes: [],
  sensorStatus: []
}

const mutations = {
  [GET_DEVICE_LIST] (state, payload) {
    state.device.list = payload.deviceList
    state.device.total_count = payload.total_count
  },
  [GET_ALARM_REASONS] (state, payload) {
    state.alarmReasons = payload.reasons
  },
  [DEVICE_LIST_CONFIG] (state, payload) {
    state.device.page_number = payload.page_number
    state.device.page_size = payload.page_size
  },
  [CLRAR_ALL_DATA] (state, payload) {
    state.alarmList = []
    state.device = {
      list: [],
      page_number: 1,
      page_size: 20,
      total_count: 0
    }
    state.deviceInfo = {}
    state.alarmReasons = []
    state.alarmBar = {
      name: '',
      xValue: [],
      data: []
    }
    state.alarmLine = {
      name: '',
      xValue: [],
      series: []
    }
  },
  [GET_SENSOR_TYPES] (state, payload) {
    state.sensorTypes = payload.list
  },
  [GET_SENSOR_STATUS] (state, payload) {
    state.sensorStatus = payload.list
  }
}

const getters = {
  device: state => state.device,
  alarmReasons: state => state.alarmReasons,
  sensorTypes: state => state.sensorTypes,
  sensorStatus: state => state.sensorStatus
}

const actions = {
  getDeviceList: ({ commit, state }, params = {}) => {
    let query = {}
    if (params.categoryCode) {
      query.categoryCode = params.categoryCode
    }
    if (params.status !== '') {
      query.status = params.status
    }
    return new Promise((resolve, reject) => {
      axois({
        method: 'post',
        url: 'api/dev/listAllByPage',
        data: {
          pagenum: state.device.page_number,
          pagesize: state.device.page_size,
          ...query
        }
      }).then(res => {
        if (res.data && res.data.datas) {
          let {list = [], totalSize} = res.data.datas
          let devices = []
          for (let i = 0; i < list.length; i++) {
            let item = list[i]
            // if (!unit && data.SM_STR) {
            //   let index = data.SM_STR.indexOf(/^\D+$/)
            //   // /^\d+(\.\d+)?$/
            //   unit = data.SM_STR.slice(index)
            // }
            devices.push({
              bottomLeft: item['BOTTOM_LEFT'],
              bottomRight: item['BOTTOM_RIGHT'],
              categoryCode: item['CATEGORY_CODE'],
              categoryName: item['CATEGORY_NAME'],
              devId: item['DEV_ID'],
              statusName: item['STATUS_NAME'],
              title: item['TITLE'],
              batteries: item['battery_list'],
              values: item['value_list'],
              commands: item['cmd_list'] || [],
              lng: item['LNG'] || 0,
              lat: item['LAT'] || 0,
              payloadId: item['PAYLOAD_ID']
            })
          }
          commit('GET_DEVICE_LIST', {
            deviceList: devices,
            total_count: totalSize
          })
        }
        resolve(res.data)
      })
    })
  },
  getAlarmReasons: ({ commit, state }, params) => {
    return new Promise((resolve, reject) => {
      axois({
        method: 'get',
        url: '/api/alarm/listReason',
        params: {
          ...params
        }
      }).then(res => {
        if (res.data && res.data.datas) {
          commit('GET_ALARM_REASONS', {
            reasons: res.data.datas
          })
        }
        resolve(res.data)
      })
    })
  },
  confirmAlarm: ({ commit, state }, params) => {
    return new Promise((resolve, reject) => {
      axois({
        method: 'post',
        url: '/api/alarm/confirm',
        data: {
          ...params
        }
      }).then(res => {
        resolve(res.data)
      })
    })
  },
  switchDevice: ({ commit, state }, params) => {
    return new Promise((resolve, reject) => {
      axois({
        method: 'post',
        url: '/api/dev/cmd',
        data: {
          ...params
        }
      }).then(res => {
        resolve(res.data)
      })
    })
  },
  configPage: ({commit}, parmas) => {
    commit('DEVICE_LIST_CONFIG', {page_number: parmas.page_number, page_size: parmas.page_size})
  },
  getSensorTypes: ({ commit, state }, params) => {
    return new Promise((resolve, reject) => {
      axois({
        method: 'get',
        url: '/api/devcategory/listValid',
        params: {
          ...params
        }
      }).then(res => {
        if (res.data && res.data.datas) {
          commit('GET_SENSOR_TYPES', res.data.datas)
        }
        resolve(res.data)
      })
    })
  },
  getSensorStatus: ({ commit, state }, params) => {
    return new Promise((resolve, reject) => {
      axois({
        method: 'get',
        url: 'api/dev/listDevStatus',
        params: {
          ...params
        }
      }).then(res => {
        if (res.data && res.data.datas) {
          commit('GET_SENSOR_STATUS', res.data.datas)
        }
        resolve(res.data)
      })
    })
  },
  listUpdateByPage: ({ commit, state }, params) => {
    return new Promise((resolve, reject) => {
      axois({
        method: 'post',
        url: '/api/dev/listUpdateByPage',
        data: {
          ...params
        }
      }).then(res => {
        resolve(res.data)
      })
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}



