export default {
  path: '/notify',
  name: 'notify',
  title: '通知管理',
  icon: 'iconfont icon-home',
  redirect: '/notify/list',
  component: r => require.ensure([], () => r(require('@/pages/notify/index'))),
  children: [{
    path: 'list',
    name: 'list',
    component: r => require.ensure([], () => r(require('@/pages/notify/list')))
  }, {
    path: 'rules',
    name: 'rules',
    component: r => require.ensure([], () => r(require('@/pages/notify/rules')))
  }, {
    path: 'contactList',
    name: 'contactList',
    component: r => require.ensure([], () => r(require('@/pages/notify/contactList')))
  }]
}



