<template>
  <div class="donut">
    <svg viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg" class="donut__svg">
      <circle :r="radius" :cy="cy" :cx="cx" stroke-width="2" stroke="#ccc"
              stroke-linejoin="round" stroke-linecap="round" stroke-opacity="0.7" fill="none"  transform="rotate(-90 50 50)"/>

      <circle id="donut-graph" :style="styleObject" :r="radius" :cy="cy" :cx="cx" stroke-width="4" :stroke="strokeColor"
              stroke-linejoin="round" stroke-linecap="round" stroke-opacity="0.7" fill="none"  transform="rotate(-90 50 50)"/>

      <defs>
        <linearGradient id="purple" x1="0%" y1="0%" x2="100%" y2="0%">
          <stop offset="0%" stop-color="#7a5bcf"/>
          <stop offset="100%" stop-color="#8A6FD5"/>
        </linearGradient>
      </defs>

    </svg>
    <div class="donut_content">
      <slot name="icon"></slot>
      <span>
        {{percent}}
      </span>
    </div>
  </div>
</template>
<script>
export default {
  name: 'circle_progress',
  data () {
    return {
      cx: 50,
      cy: 50,
      radius: 46
    }
  },
  props: {
    value: {
      type: Number,
      default: 82,
      required: true
    },
    min: {
      type: Number,
      default: 0
    },
    max: {
      type: Number,
      default: 100
    },
    status: {
      type: String,
      default: 'info'
    }
  },
  methods: {
  },
  computed: {
    circumference: function () {
      return 2 * Math.PI * this.radius
    },
    styleObject: function () {
      return {
        'stroke-dasharray': this.circumference,
        'stroke-dashoffset': this.circumference - ((this.value / (this.max - this.min)) * this.circumference)
      }
    },
    strokeColor: function () {
      switch (this.status) {
        case 'info': return '#4C94FF'
        case 'danger': return '#FA5760'
        default: return '#4C94FF'
      }
    },
    percent: function () {
      return `${((this.value / (this.max - this.min)) * 100).toFixed(2)}%`
    }
  }
}
</script>
<style lang="less">
  .donut {
    position: relative;
    width: 100%;
    height: 100%;
    max-width: 152px;
    max-height: 152px;
  }

  .donut_content {
    width: 100%;
    text-align: center;
    position: absolute;
    left: 0;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
  }
</style>



