<template>
  <div class="echarts">
    <IEcharts 
      :option="area"
      :loading="loading"
      @ready="onReady"
      @click="onClick" />
  </div>
</template>

<script type="text/babel">
import IEcharts from 'vue-echarts-v3/src/full.js'
var echarts = require('echarts')

export default {
  name: 'AreaChart',
  components: {
    IEcharts
  },
  props: {
    name: {
      type: String,
      required: true,
      default: 'line'
    },
    xValue: {
      type: Array,
      required: true,
      default: () => []
    },
    series: {
      type: Array,
      required: true,
      default: () => {
        return []
      }
    },
    legend: {
      type: Array || String,
      required: false,
      default: ''
    }
  },
  computed: {
    area: function () {
      return {
        tooltip: {
          trigger: 'axis',
          position: function (pt) {
            return [pt[0], '10%']
          }
        },
        title: {
          left: 'center',
          text: this.name,
          textStyle: {
            color: '#fff'
          }
        },
        /* legend: {
          data: Array.isArray(this.legend) ? this.legend : [this.legend]
        }, */
        toolbox: {
          /* feature: {
            dataZoom: {
              yAxisIndex: 'none'
            },
            restore: {},
            saveAsImage: {}
          } */
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: this.xValue,
          axisLabel: {
            textStyle: {
              color: '#fff'
            }
          }
        },
        yAxis: {
          type: 'value',
          boundaryGap: [0, '100%'],
          axisLabel: {
            textStyle: {
              color: '#fff'
            }
          }
        },
        dataZoom: [{
          type: 'inside',
          start: 0,
          end: 10
        }, {
          start: 0,
          end: 10,
          handleIcon: 'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
          handleSize: '80%',
          handleStyle: {
            color: '#fff',
            shadowBlur: 3,
            shadowColor: 'rgba(0, 0, 0, 0.6)',
            shadowOffsetX: 2,
            shadowOffsetY: 2
          }
        }],
        series: (() => {
          return this.series.map(s => {
            return {
              name: s.name,
              type: s.type,
              smooth: true,
              symbol: 'none',
              sampling: 'average',
              itemStyle: {
                color: 'rgb(255, 70, 131)'
              },
              areaStyle: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                  offset: 0,
                  color: 'rgb(255, 158, 68)'
                }, {
                  offset: 1,
                  color: 'rgb(255, 70, 131)'
                }])
              },
              data: s.data
            }
          })
        })()
      }
    }
  },
  data () {
    return {
      loading: false,
      data: [],
      date: ['2018/7/10']
    }
  },
  methods: {
    onReady (instance, ECharts) {
      console.log(instance, ECharts)
    },
    onClick (event, instance, ECharts) {
      console.log(arguments)
    }
  }
}
</script>

<style scoped>
.echarts {
  height: 200px;
}
</style>



