import axois from '@/components/axios'

const GET_EVENT_LIST = 'GET_EVENT_LIST'
const GET_EVENT_DETAIL = 'GET_EVENT_DETAIL'

const state = {
  events: {
    list: [],
    total_count: 0
  },
  detail: {
    timeLine: []
  }
}

const mutations = {
  [GET_EVENT_LIST] (state, payload) {
    state.events.list = payload.list
    state.events.total_count = payload.total_count
  },
  [GET_EVENT_DETAIL] (state, payload) {
    state.detail.timeLine = payload.timeline
  }
}

const getters = {
  events: state => state.events,
  detail: state => state.detail
}

const actions = {
  getEventList: ({ commit, state }, params) => {
    return new Promise((resolve, reject) => {
      axois({
        method: 'post',
        url: 'api/alarm/listByPage',
        data: {
          ...params
        }
      }).then(res => {
        let eventList = []
        let {datas: {list, totalSize}} = res.data
        if (list && list.length > 0) {
          for (let item of list) {
            let {alarm, device} = item || {}
            eventList.push({
              equipment: device.CATEGORY_NAME,
              id: alarm.ID,
              time: alarm.CREATED_STR_OLD,
              type: alarm.ALARM_LEVEL_STR,
              name: alarm.NAME,
              location: device.LOC_DESC,
              confirmStatus: alarm.CONFIRM_STATUS,
              confirmStatusStr: alarm.CONFIRM_STATUS_STR,
              suggestion: alarm.SUGGESTION,
              lastModifyTime: alarm.LAST_MODIFIED_STR_OLD,
              deviceName: device.NAME,
              latitude: device.LAT,
              longitude: device.LNG,
              deveui: device.DEVEUI,
              deviceStatus: device.STATUS,
              deviceStatusName: device.STATUS_NAME,
              desc: device.DESC,
              categoryCode: device.CATEGORY_CODE
            })
          }
        }
        commit('GET_EVENT_LIST', {
          list: eventList,
          total_count: totalSize
        })
        resolve(res.data)
      })
    })
  },
  getEventDetail: ({ commit, state }, params) => {
    return new Promise((resolve, reject) => {
      axois({
        method: 'get',
        url: 'api/alarm/detail',
        params: {
          ...params
        }
      }).then(res => {
        // console.log(res)
        let {datas: {alarm, devData, device, timeline}} = res.data
        commit('GET_EVENT_DETAIL', {
          /* detail: {
            alarm,
            devData,
            device,
            timeline
          } */
          timeline
        })
        resolve(res.data)
      })
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}



