import axois from '@/components/axios'
import router from '../../router'
import store from '../index'

const state = {
  account: null,
  userInfo: null,
  features: []
}

const mutations = {
  setLoginInfo (state, payload) {
    state.userInfo = payload.userInfo
    // state.account = payload.account
    // state.features = payload.features
  }
}

const getters = {
  getAccount (state) {
    return state.account
  }
}

const actions = {
  /**
     * 登录
     */
  loginSystem: ({ commit, state }, params) => {
    return new Promise((resolve, reject) => {
      axois({
        method: 'post',
        url: 'api/sec/login',
        data: {
          ...params
        }
      }).then(res => {
        if (res.data.result === 0) {
          commit('setLoginInfo', {
            userInfo: res.data.datas
          })
          resolve(res.data.datas)
        }
        resolve(res)
      }).catch((error) => {
        reject(error)
      })
    })
  },
  features: ({ commit, state }) => {
    return new Promise((resolve, reject) => {
      let routers = router.getFeatures(['login', 'profile', 'alarm', 'monitor', 'deviceInfo'])
      // let routers = router.getFeatures(['login', 'monitor'])
      router.activeFeatures(routers)
      store.activeFeatures(routers)
      resolve(routers)
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}



