<template>
  <div class="echarts">
    <IEcharts
      :option="ring"
      :loading="loading"
      @ready="onReady"
      @click="onClick"
    />
  </div>
</template>

<script type="text/babel">
import IEcharts from 'vue-echarts-v3/src/full.js'
export default {
  name: 'ring',
  components: {
    IEcharts
  },
  props: {
    name: {
      type: String,
      required: true,
      default: 'bar'
    },
    series: {
      type: Object,
      required: true,
      default: () => {
        return {
          name: '',
          data: []
        }
      }
    },
    legend: {
      type: Array,
      required: false,
      default: () => []
    }
  },
  computed: {
    ring: function () {
      return {
        title: {
          text: this.name,
          subtext: '',
          textStyle: {
            color: '#fff'
          }
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          x: 'left',
          data: this.legend
        },
        series: [
          {
            name: this.series.name,
            type: 'pie',
            radius: ['40%', '55%'],
            label: {
              normal: {
                formatter: '{a|{a}}{abg|}\n{hr|}\n  {b|{b}：}{c}  {per|{d}%}  ',
                backgroundColor: '#eee',
                borderColor: '#aaa',
                borderWidth: 1,
                borderRadius: 4,
                // shadowBlur:3,
                // shadowOffsetX: 2,
                // shadowOffsetY: 2,
                // shadowColor: '#999',
                // padding: [0, 7],
                rich: {
                  a: {
                    color: '#999',
                    lineHeight: 22,
                    align: 'center'
                  },
                  // abg: {
                  //     backgroundColor: '#333',
                  //     width: '100%',
                  //     align: 'right',
                  //     height: 22,
                  //     borderRadius: [4, 4, 0, 0]
                  // },
                  hr: {
                    borderColor: '#aaa',
                    width: '100%',
                    borderWidth: 0.5,
                    height: 0
                  },
                  b: {
                    fontSize: 16,
                    lineHeight: 33
                  },
                  per: {
                    color: '#eee',
                    backgroundColor: '#334455',
                    padding: [2, 4],
                    borderRadius: 2
                  }
                }
              }
            },
            data: this.series.data
          }
        ]
      }
    }
  },
  data: () => ({
    loading: false
  }),
  methods: {
    onReady (instance, ECharts) {
      console.log(instance, ECharts)
    },
    onClick (event, instance, ECharts) {
      console.log(arguments)
    }
  }
}
</script>

<style scoped>
  .echarts {
    /* width: 400px; */
    height: 200px;
  }
</style>



