export default {
  path: 'event',
  name: 'event',
  title: '事件管理',
  icon: 'iconfont icon-alarm1',
  component: r => require(['@/pages/event'], r),
  children: [{
    path: 'list',
    name: 'eventList',
    component: r => require.ensure([], () => r(require('@/pages/event/list')))
  }, {
    path: 'rules',
    name: 'eventRules',
    component: r => require.ensure([], () => r(require('@/pages/event/rules')))
  }]
}



