<template>
  <div class="echarts">
    <IEcharts
      :option="line"
      :loading="loading"
      @ready="onReady"
      @click="onClick"
    />
  </div>
</template>

<script type="text/babel">
import IEcharts from 'vue-echarts-v3/src/full.js'
export default {
  name: 'line',
  components: {
    IEcharts
  },
  props: {
    name: {
      type: String,
      required: true,
      default: 'line'
    },
    xValue: {
      type: Array,
      required: true,
      default: () => []
    },
    series: {
      type: Array,
      required: true,
      default: () => {
        return []
      }
    },
    legend: {
      type: Array,
      required: false,
      default: () => ['告警']
    }
  },
  computed: {
    line: function () {
      // console.log(this.legend)
      return {
        title: {
          text: this.name,
          subtext: '',
          textStyle: {
            color: '#fff'
          }
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: this.legend,
          textStyle: {
            color: '#fff'
          }
        },
        calculable: true,
        xAxis: [
          {
            type: 'category',
            data: this.xValue,
            axisLabel: {
              textStyle: {
                color: '#fff'
              }
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            axisLabel: {
              textStyle: {
                color: '#fff'
              }
            }
          }
        ],
        series: this.series
      }
    }
  },
  data: () => ({
    loading: false
  }),
  methods: {
    onReady (instance, ECharts) {
      console.log(instance, ECharts)
    },
    onClick (event, instance, ECharts) {
      console.log(arguments)
    }
  }
}
</script>

<style scoped>
  .echarts {
    /* width: 400px; */
    height: 200px;
  }
</style>



