import barComponent from './Bar'
import lineComonent from './Line'
import ringComponent from './Ring'
import circleProgress from './circleProgress'
import areaComponent from './Area'
import pie from './Pie'
import Vue from 'vue'

Vue.component('cl-bar', barComponent)
Vue.component('cl-line', lineComonent)
Vue.component('cl-ring', ringComponent)
Vue.component('cl-circle', circleProgress)
Vue.component('cl-area', areaComponent)
Vue.component('cl-pie', pie)



