import axois from '@/components/axios'

/** 获取设备详情 **/
const GET_DEVICE_DETAIL = 'GET_DEVICE_DETAIL'
/** 获取此设备的告警列表 */
const GET_DEVICE_ALARM_LIST = 'GET_DEVICE_ALARM_LIST'
/** 获取此设备的历史指标值 */
const GET_DEVICE_HISTORY_DATA = 'GET_DEVICE_HISTORY_DATA'
/** 告警列表分页配置 */
export const CONFIG_ALARM_LIST = 'CONFIG_ALARM_LIST'
/** 清除所有数据  */
export const CLRAR_ALL_DATA = 'CLRAR_ALL_DATA'

const state = {
  detail: {},
  userList: [],
  alarm: {
    list: [],
    page_number: 1,
    page_size: 20,
    total_count: 0
  },
  sensorHistory: []
}

const mutations = {
  [GET_DEVICE_DETAIL] (state, payload) {
    state.detail = payload.detail
    state.userList = payload.userList
  },
  [GET_DEVICE_ALARM_LIST] (state, payload) {
    state.alarm.list = payload.alarmList
    state.alarm.total_count = payload.total_count
  },
  [GET_DEVICE_HISTORY_DATA] (state, payload) {
    state.sensorHistory = payload.history
  },
  [CONFIG_ALARM_LIST] (state, payload) {
    state.alarm.page_number = payload.page_number
    state.alarm.page_size = payload.page_size
  },
  [CLRAR_ALL_DATA] (state, payload) {
    state.detail = {}
    state.userList = []
    state.alarm = {
      list: [],
      page_number: 1,
      page_size: 20,
      total_count: 0
    }
    state.sensorHistory = []
  }
}

const getters = {
  detail: state => state.detail,
  userList: state => state.userList,
  alarmList: state => state.alarm.list,
  alarm: state => state.alarm,
  sensorHistory: state => state.sensorHistory
}

const actions = {
  getDeviceDetail: ({ commit, state }, params) => {
    return new Promise((resolve, reject) => {
      axois({
        method: 'get',
        url: '/api/dev/detail',
        params: {
          ...params
        }
      }).then(res => {
        if (res.data && res.data.datas && res.data.datas.dev) {
          let rs = res.data.datas || {}
          commit('GET_DEVICE_DETAIL', {
            detail: rs.dev,
            userList: rs.devUserList
          })
        }
        resolve(res.data)
      })
    })
  },
  getDeviceAlarmList: ({ commit, state }, params) => {
    return new Promise((resolve, reject) => {
      axois({
        method: 'post',
        url: 'api/alarm/listByPage',
        data: {
          ...params,
          pagenum: state.alarm.page_number,
          pagesize: state.alarm.page_size
        }
      }).then(res => {
        let alarmList = []
        if (res.data.datas) {
          let {list = [], totalSize = 0} = res.data.datas
          for (let i = 0; i < list.length; i++) {
            let {alarm, device} = list[i] || {}
            alarmList.push({
              equipment: device.CATEGORY_NAME,
              id: alarm.ID,
              time: alarm.CREATED_STR_OLD,
              type: alarm.ALARM_LEVEL_STR,
              name: alarm.NAME,
              location: device.LOC_DESC,
              confirmStatus: alarm.CONFIRM_STATUS,
              confirmStatusStr: alarm.CONFIRM_STATUS_STR,
              suggestion: alarm.SUGGESTION,
              lastModifyTime: alarm.LAST_MODIFIED_STR_OLD,
              deviceName: device.NAME,
              latitude: device.LAT,
              longitude: device.LNG,
              deveui: device.DEVEUI,
              deviceStatus: device.STATUS,
              categoryCode: device.CATEGORY_CODE
            })
          }
          commit('GET_DEVICE_ALARM_LIST', {
            alarmList,
            total_count: totalSize
          })
        }
        resolve(res.data)
      })
    })
  },
  getDeviceHistoryData: ({ commit, state }, params) => {
    return new Promise((resolve, reject) => {
      axois({
        method: 'get',
        url: '/api/dev/history',
        params: {
          ...params
        }
      }).then(res => {
        let rs = []
        let { datas, result } = res.data
        if (result === 0 && datas && datas.itemList) {
          for (let i = 0; i < datas.itemList.length; i++) {
            let {NAME, VALUE_TYPE} = datas.itemList[i]
            // let dt = datas[NAME] || []
            let xValue = []
            let yValue = []
            if (datas[NAME] && datas[NAME].length > 0) {
              datas[NAME].forEach(ele => {
                xValue.push(ele.X_DATA)
                yValue.push(ele[VALUE_TYPE])
              })
            }
            rs.push({
              name: NAME,
              xValue,
              series: [{
                name: NAME,
                type: 'line',
                stack: '总量',
                data: yValue
              }],
              legend: NAME
            })
          }
        }
        commit('GET_DEVICE_HISTORY_DATA', {
          history: rs
        })
        resolve(rs)
      })
    })
  },
  configPage: ({commit}, parmas) => {
    commit('CONFIG_ALARM_LIST', {page_number: parmas.page_number, page_size: parmas.page_size})
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}



