export default {
  path: '/device',
  name: 'device',
  title: '设备管理',
  icon: 'iconfont icon-home',
  redirect: '/device/list',
  component: r => require.ensure([], () => r(require('@/pages/device/index'))),
  children: [{
    path: 'list',
    name: 'deviceList',
    component: r => require.ensure([], () => r(require('@/pages/device/list')))
  }, {
    path: 'category',
    name: 'category',
    component: r => require.ensure([], () => r(require('@/pages/device/category')))
  }, {
    path: 'producer',
    name: 'producer',
    component: r => require.ensure([], () => r(require('@/pages/device/producer')))
  }, {
    path: 'deploy',
    name: 'deploy',
    component: r => require.ensure([], () => r(require('@/pages/device/deploy')))
  }, {
    path: 'log',
    name: 'log',
    component: r => require.ensure([], () => r(require('@/pages/device/log')))
  }, {
    path: 'coderDecoder',
    name: 'coderDecoder',
    component: r => require.ensure([], () => r(require('@/pages/device/coderDecoder')))
  }]
}



