import Vue from 'vue'
import Vuex from 'vuex'

const files = require.context('.', true, /\.js$/)
let modules = {}
files.keys().forEach(key => {
  let module = files(key).default
  if (module && module.namespaced) {
    let path = key.replace(/(\.\/|(\/[^\/]+\.)js|\.js)/g, '')
    modules[path] = module
  }
})

Vue.use(Vuex)
const store = new Vuex.Store({
  modules: {}
})

store.activeFeatures = function (features) {}

for (let m in modules) {
  store.registerModule(m, modules[m])
}

const quit = () => {
  let json = JSON.stringify({
    common: store.state.common
  })
  localStorage.setItem('security_guard', json)
  // TODO 后台请求
}

let json = localStorage.getItem('security_guard')
if (!json) {
  // TODO 后台请求
}
let state = {}
if (json) {
  state = JSON.parse(json)
  if (state) {
    // store.state.login = state.login
    store.state.common = state.common
  }
}

window.onunload = function () {
  quit()
  return true
}

export default store



