export default {
  path: '/account',
  name: 'account',
  title: '设备管理',
  icon: 'iconfont icon-home',
  redirect: '/account/pending',
  component: r => require.ensure([], () => r(require('@/pages/account/index'))),
  children: [{
    path: 'pending',
    name: 'pending',
    component: r => require.ensure([], () => r(require('@/pages/account/pending')))
  }, {
    path: 'list',
    name: 'list',
    component: r => require.ensure([], () => r(require('@/pages/account/list')))
  }, {
    path: 'admin',
    name: 'admin',
    component: r => require.ensure([], () => r(require('@/pages/account/admin')))
  }]
}



