import Vue from 'vue'
import Router from 'vue-router'
import { sync } from 'vuex-router-sync'
import store from '../store'

let routes = {}
let childrens = []
const files = require.context('.', true, /\.js$/)
files.keys().forEach(key => {
  let route = files(key).default
  if (route && route.path) {
    routes[route.path] = route
    childrens.push(route)
  }
})

Vue.use(Router)

let router = new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'main',
      component: r => require(['@/pages/index'], r),
      children: childrens,
      redirect: {name: 'monitor'}
    }, {
      path: '/login',
      name: 'login',
      component: r => require(['@/pages/login'], r)
    }, {
      path: '*',
      name: '404',
      component: r => require(['@/pages/error/404.vue'], r)
    }
  ]
})

// 同步store和router
sync(store, router)

router.activeFeatures = function (features) {}

router.getFeatures = function (paths) {
  return (paths || []).map(path => {
    return routes[path]
  })
}

export default router



